import {environment} from "@env/environment";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "./logger";
import {WebPlatform} from "./platform";
import {VsanUiUtils} from "./vsan-util";

export class ModalBuilder {

   static readonly WIDTH: number = 0;
   static readonly HEIGHT: number = 1;

   static readonly DEFAULT_SIZE: number[] = [1000, 530];
   static readonly WIZARD_XL: number[] = [1200, 700];

   static readonly APP_URL: string = (environment.repa && environment.repaExtension)
         ? "index.html"
         : "/ui/h5-vsan-ui/";

   private size: number[] = ModalBuilder.DEFAULT_SIZE;

   // When null, the modal dialog will be headless and the docked
   // view is expected to provide a title bar.
   private title: string = null;

   private accessibilityTitle: string = null;

   private urlParams: any = {};

   private eventTarget: EventTarget;

   constructor(private appId: string) {
   }

   setDimensions(dimensions: number[]): ModalBuilder {
      if (!dimensions || dimensions.length !== 2) {
         Logger.warn("invalid modal dimensions", dimensions);
         dimensions = ModalBuilder.DEFAULT_SIZE;
      }
      return this.setSize(dimensions[ModalBuilder.WIDTH], dimensions[ModalBuilder.HEIGHT]);
   }

   setSize(width: number, height: number): ModalBuilder {
      this.size = [width, height];
      return this;
   }

   setUrlParameters(params: any): ModalBuilder {
      this.urlParams = params;
      return this;
   }

   /**
    * Set to null if the view provides its own title bar
    */
   setTitle(title: string): ModalBuilder {
      this.title = title;
      return this;
   }

   setAccessibilityTitle(accessibilityTitle: string): ModalBuilder {
      this.accessibilityTitle = accessibilityTitle;
      return this;
   }

   /**
    * Set explicit event target if it is changed before opening the modal.
    * For example when making a preceding request the event target is the XMLHttpRequestÂ object.
    */
   setEventTarget(eventTarget: EventTarget): ModalBuilder {
      this.eventTarget = eventTarget;
      return this;
   }

   /**
    * Opens a modal dialog and returns a promise to collect the result; On close the target element is focused.
    * @param context - any JS data object, can be retrieved by WebPlatform.getModalContext()
    * @returns {Promise<any>} - Invoked if the dialog invokes WebPlatform.closeModalDialog(arg)
    *       with non-null arg. The promise is never rejected.
    */
   open(context?: any): Promise<any> {
      return new Promise<any>((accept) => {
         VsanUiUtils.pluginContext.targetElement = this.eventTarget ? this.eventTarget :
               (event && event.target ? event.target : null);
         VsanUiUtils.pluginContext.modalResultHandler = (data?: any) => {
            if (typeof data !== "undefined") {
               // Deep copy of the object was required to resolve an issue with object's prototype overriding when
               // passing complex objects in IE11/Edge; Keep it for safety.
               accept(JSON.parse(JSON.stringify(data)));
            } else if (VsanUiUtils.pluginContext.targetElement && VsanUiUtils.pluginContext.targetElement.focus) {
               // On cancel just focus the target element. Timeout is required as otherwise the focus may be
               // consumed by the mouse click.
               setTimeout(() => VsanUiUtils.pluginContext.targetElement.focus(), 100);
            }
         };

         let actionUrl: string = `${ModalBuilder.APP_URL}?viewId=${this.appId}`;

         let separator: string = "?";
         for (const param in this.urlParams) {
            actionUrl += separator + param + "=" + this.urlParams[param];
            separator = "&";
         }

         WebPlatform.openModalDialog(this.title,
               actionUrl,
               ManagedObject.contextObjectUid,
               this.size[0], this.size[1],
               this.title != null,
               VsanUiUtils.pluginContext.modalResultHandler /* onClose */,
               context /* retrieve with Platform.getModalContext() */,
               this.accessibilityTitle);
      });
   }

}
